<script>
import { ref } from "vue";
import { useRouter } from "vue-router";
import LoaderBar from "../components/LoaderBar.vue";

export default {
  name: "Incidencias",
  components: { LoaderBar },

  setup(props) {
    const router = useRouter();

    var fecha = new Date();
    var mes = fecha.getMonth() + 1;
    var dia = fecha.getDate();
    var ano = fecha.getFullYear();
    if (dia < 10) dia = "0" + dia;
    if (mes < 10) mes = "0" + mes;
    const fechaFin = ref(ano + "-" + mes + "-" + dia);

    fecha.setDate(fecha.getDate() - 7);
    var mes = fecha.getMonth() + 1;
    var dia = fecha.getDate();
    var ano = fecha.getFullYear();
    if (dia < 10) dia = "0" + dia;
    if (mes < 10) mes = "0" + mes;
    const fechaInicio = ref(ano + "-" + mes + "-" + dia);

    // ARRAYS
    const arrayData = ref([]);
    const arraySecciones = ref([]);
    const arrayRanking = ref([]);
    const idDestino = ref(localStorage.getItem("idDestino"));
    const idUsuario = ref(localStorage.getItem("usuario"));
    const porcentaje = ref(0);

    const ranking = async () => {
      if (idDestino.value == 10) {
        router.push("/america");
      }

      try {
        const resp = await fetch(
          `../apis/reportes.php?action=ranking&idDestino=${idDestino.value}&idUsuario=${idUsuario.value}&fechaInicio=${fechaInicio.value}&fechaFin=${fechaFin.value}`
        );
        arrayRanking.value = await resp.json();
      } catch (error) {
        console.log(error);
      }
    };
    ranking();

    const fetchDatos = async () => {
      if (idDestino.value == 10) {
        return;
      }
      try {
        let myTimer = setInterval(() => {
          porcentaje.value += 7;
          if (porcentaje.value >= 99) {
            porcentaje.value = 0;
          }
        }, 1000);
        const res = await fetch(
          `../apis/reportes.php?action=reporte&idDestino=${idDestino.value}&idUsuario=${idUsuario.value}&fechaInicio=${fechaInicio.value}&fechaFin=${fechaFin.value}`
        );
        arrayData.value = await res.json();
        clearInterval(myTimer);
        porcentaje.value = 100;
      } catch (error) {
        porcentaje.value = 100;
        console.log(error);
      }
    };
    fetchDatos();

    return {
      arrayData,
      arraySecciones,
      arrayRanking,
      fechaInicio,
      fechaFin,
      ranking,
      fetchDatos,
      idDestino,
      porcentaje,
    };
  },
};
</script>

